<template>
    <div>
        <h4 class="text-center"></h4>
        <el-row type="flex" justify="space-around" style="margin-bottom:10px">
            <el-col :span="18">
                <!-- <el-input v-model="keyword" clearable placeholder="" style="width:180px"
                    @keydown.enter.native="searchAdmin"></el-input> -->
                <el-button type="primary" plain @click="searchAdmin"></el-button>
                <el-button type="info" plain @click="requestAdminData"></el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="success" plain @click="addAdminVisible = true"></el-button>
            </el-col>
        </el-row>
        <!--  -->
        <admin-table v-bind:adminlist="list" style="margin-bottom:10px" @onEdit="onEdit"></admin-table>
        <!--  -->
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
        <!--  -->
        <add-admin v-bind:dialogVisible="addAdminVisible" @close="addAdminVisible = false"
            @addSuccess="onAddSuccess"></add-admin>
        <!--  -->
        <el-dialog title="" :visible.sync="ifShowEdit">
            <adit-admin v-bind:account="editData" v-if="ifShowEdit" @onCancel="ifShowEdit = false"></adit-admin>
        </el-dialog>
        
    </div>
</template>

<script>
import AdminTable from '../admin/AdminTable.vue'
import AddAdmin from '../admin/AddAdmin.vue'
import AditAdmin from '../admin/EditAdmin.vue'

export default {
    components: { AdminTable, AddAdmin, AditAdmin },
    name: 'MyAdmin',
    data() {
        return {
            addAdminVisible: false,
            currentPage: 1,//
            total: 0,//
            pageSize: 5,//
            list: [],//
            editData: {},//
            ifShowEdit: false//
        }
    },
    methods: {
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.requestAdminData({ currentPage: val })
        },
        searchAdmin() {
            let keyword = this.keyword
            this.requestAdminData({ keyword })
        },
        requestAdminData({ currentPage = 1, pageSize = this.pageSize, role = 1 }, successMsg = '') {
            console.log(currentPage, pageSize)
            this.$https.get('/user/page', {
            // this.$https.get('/data/test1.json', {
                params: {
                    currentPage,
                    pageSize,
                    role
                }
            })
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: successMsg,
                            type: 'success'
                        })
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                        console.log(this.list, this.total);
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
                .catch((error) => {
                    console.log(error);
                    this.$message.error('')
                })
        },
        onAddSuccess(msg) {
            this.requestAdminData({}, msg)
        },
        onEdit(index) {
            //
            this.list[index].password = ''
            //
            this.editData = this.list[index]
            this.ifShowEdit = true
        }

    },
    mounted: function () {
        //
        this.requestAdminData({})
    }
}
</script>

<style lang="less" scoped></style>