<template>
    <div>
        <h4 class="text-center"></h4>
        <el-card class="box-card" style="margin-top: 20px;">
            <div slot="header" class="clearfix">
                <span style="font-size: 16px;"></span>
            </div>
            <div>
                <el-input v-model="addCategoryName" placeholder="" style="width:221px"></el-input>
                <el-button @click="addCategory" type="primary" :disabled="addCategoryName === ''"></el-button>
            </div>
        </el-card>
        <el-divider></el-divider>
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span style="font-size: 16px;"></span>
            </div>
            <div>
                <category-select v-model="categoryId" style="margin-bottom: 20px;"></category-select>
                <el-input v-model="newCategoryName" placeholder="" :disabled="!categoryId"
                    style="width:221px"></el-input>
                <el-button @click="editCategory" type="primary" :disabled="newCategoryName === ''"></el-button>
                <el-button @click="onDeleteClick" type="danger" icon="el-icon-delete" circle
                    :disabled="!categoryId"></el-button>
            </div>
        </el-card>

    </div>
</template>
  
<script>
import CategorySelect from '../category/CategorySelect.vue'

export default {
    components: { CategorySelect },
    name: 'MyCategory',
    data() {
        return {
            addCategoryName: '',
            newCategoryName: '',
            categoryId: null
        }
    },
    methods: {
        addCategory() {
            // this.$https.get('/data/test.json')
            this.$https.post('/category', {
                categoryName: this.addCategoryName,
                parentId: 0
            })
                .then((response) => {
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message.error(res.msg)
                    }

                })
                .catch((error) => {
                    this.$message.error('')
                })
        },
        editCategory() {
            this.$https.put('/category', {
                categoryId: this.categoryId,
                categoryName: this.newCategoryName,
                parentId: 0
            })
                .then((response) => {
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message.error(res.msg)
                    }

                })
                .catch((error) => {
                    this.$message.error('')
                })

        },
        onDeleteClick() {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.deleteCategory()
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        deleteCategory() {
            this.$https.delete('/category/' + this.categoryId)
                .then((response) => {
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            type: 'success',
                            message: ''
                        })
                    } else {
                        this.$message.error(res.msg)
                    }

                })
                .catch((error) => {
                    this.$message.error('')
                })

        }
    }
}
</script>
  
<style lang="less" scoped></style>