<template>
    <div>
        <el-form class="form_container" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="ID" :label-width="formLabelWidth">
                        <el-input :placeholder="ruleForm.userId" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input :placeholder="ruleForm.role" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth" prop="username">
                        <el-input v-model="ruleForm.username" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="ruleForm.name" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="ruleForm.gender" placeholder="" style="width:80px">
                            <el-option v-for="item in genderOptions" :key="item.value" :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="" format="yyyy  MM  dd "
                            value-format="yyyy-MM-dd">
                        </el-date-picker>
                        <!-- <el-input v-model="ruleForm.birthday" placeholder="" autocomplete="off"></el-input> -->
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" :label-width="formLabelWidth" prop="password">
                <el-input v-model="ruleForm.password" placeholder="" show-password autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="email">
                <el-input v-model="ruleForm.email" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="phone">
                <el-input v-model="ruleForm.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <div style="margin-left: 60%;">
                <el-button @click="onCancel"> </el-button>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            </div>
        </el-form>
    </div>
</template>

<script>

export default {
    name: 'EditAdmin',
    props: {
        adminlist: {
            type: Array
        },
        account: {
            type: Object,
            default: () => {
                return {
                    updateUser: 0, //userId
                    userId: 0,
                    username: "",
                    name: "",
                    gender: "-1",
                    password: '',
                    birthday: "",
                    role: "",
                    phone: "",
                    email: "",
                }
            }
        }
    },
    computed: {

    },
    data() {
        return {
            genderOptions: [{
                value: '1',
                label: ''
            }, {
                value: '0',
                label: ''
            }, {
                value: '-1',
                label: ''
            }],
            ruleForm: this.account,
            formLabelWidth: '120px',
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                password: [
                    // { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15 ', trigger: ['blur', 'change'] }
                ],
                email: [
                    { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
                phone: [
                    { min: 11, max: 11, message: "11", trigger: ['blur'] }
                ]

            }
        }
    },
    methods: {
        onCancel() {
            this.$emit('onCancel')
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.putEmdit()
                }
            })
        },
        putEmdit() {
            let req = this.ruleForm;
            //ID ()
            // let loginID = localStorage.getItem('loginID')
            // if (!loginID){
            //     this.$message.error('')
            //     req.updateUser = null;
            // } else {
            //     //updateUser
            //     req.updateUser = loginID;
            // }
            this.$https.put('/user', req)
            // this.$https.get('/data/test.json', req)
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: '',
                            type: 'success'
                        })
                        //
                        this.onCancel()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
                .catch((error) => {
                    console.log(error);
                    this.$message.error('')
                })
        }

    }
}
</script>

<style lang="less" scoped>
.form_container {
    width: 95%;
    margin: 0 auto;
}
</style>