<template>
    <div class="goods_container">
        <h4 class="text-center"></h4>
        <!--  -->
        <el-row type="flex" justify="space-around" style="margin-bottom:10px">
            <!--  -->
            <el-col :span="5">
                <el-input v-model="keyword" clearable placeholder="" style="width:180px"
                    @keydown.enter.native="searchGoods"></el-input>
                <el-button type="primary" plain @click="searchGoods" :disabled="!keyword"></el-button>
            </el-col>
            <!--  -->
            <el-col :span="5">
                <category-select placeholder="" v-model="categoryId" style="display: inline-block; width: 120px;"></category-select>
                <el-button type="primary" plain :disabled="!categoryId" @click="showCategoryGoods"></el-button>
            </el-col>
            <!--  -->
            <el-col :span="10">
                <el-button  plain @click="showAllGoods"></el-button>
            </el-col>
            <!--  -->
            <el-col :span="2">
                <el-button type="success" plain @click="addGoodsVisible = true"></el-button>
            </el-col>
        </el-row>
        <!--  -->
        <goods-table v-bind:goodslist="list" style="margin-bottom:10px"
            @cancelEmdit="requestGoodsData({},'')"></goods-table>
        <!--  -->
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
        <!--  -->
        <add-goods v-bind:dialogVisible="addGoodsVisible" @close="addGoodsVisible = false"
            @addSuccess="onAddSuccess"></add-goods>
    </div>
</template>

<script>
import CategorySelect from '../category/CategorySelect.vue';
import GoodsTable from '../goods/GoodsTable.vue';
import AddGoods from '../goods/AddGoods.vue';

export default {
    components: { GoodsTable, AddGoods, CategorySelect },
    name: 'MyGoods',
    data() {
        return {
            addGoodsVisible: false,//
            currentPage: 1,//
            total: 0,//
            pageSize: 5,//
            keyword: null,//
            categoryId: null,//ID
            list: [],//
        }
    },
    methods: {
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.requestGoodsData({ currentPage: val })
        },
        searchGoods() {
            let keyword = this.keyword
            this.requestGoodsData({ keyword })
        },
        showCategoryGoods() {
            let categoryId = this.categoryId
            this.requestGoodsData({ categoryId })
        },
        showAllGoods(){
            //ID = null,
            this.categoryId = null
            this.keyword = ''
            this.requestGoodsData({})
        },
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '') {
            console.log(currentPage, pageSize, keyword, categoryId)
            this.$https.get('/product/page', {
            // this.$https.get('/data/test.json', {
                params: {
                    currentPage,
                    pageSize,
                    keyword,
                    categoryId
                }
            })
                .then(function (response) {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: successMsg,
                            type: 'success'
                        })
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                        console.log(this.list, this.total);
                    } else {
                        this.$message.error(response.data.msg)
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('')
                }.bind(this))
        },
        onAddSuccess(msg) {
            this.requestGoodsData({}, msg)
        }

    },
    mounted: function () {
        //
        this.requestGoodsData({})
    }
}
</script>

<style lang="less" scoped></style>