<template>
    <el-dialog title="" :visible="dialogVisible" @close="onClose" width="30%">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" style="width:90%">
            <el-form-item label="" :label-width="formLabelWidth" prop="username">
                <el-input v-model="ruleForm.username" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="password">
                <el-input v-model="ruleForm.password" placeholder="" show-password autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="repeatPassword">
                <el-input v-model="ruleForm.repeatPassword" placeholder="" show-password
                    autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="email">
                <el-input v-model="ruleForm.email" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="phone">
                <el-input v-model="ruleForm.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="resetForm('ruleForm')"></el-button>
            <el-button @click="onClose"> </el-button>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
    props: {
        dialogVisible: {
            type: Boolean,
            default: false
        }
    },
    data() {
        let validatePass2 = (rule, value, callback) => {
            if (value !== this.ruleForm.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                username: '',
                password: '',
                repeatPassword: '',
                phone: null,
                email: null,
            },
            formLabelWidth: '120px',
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15 ', trigger: ['blur', 'change'] }
                ],
                repeatPassword: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: validatePass2, trigger: 'change' }
                ],
                email: [
                    { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
                phone: [
                    { min: 11,max: 11, message: "11", trigger: ['blur', 'change'] }
                ]

            }
        };
    },
    methods: {
        onClose() {
            //
            this.resetForm('ruleForm')
            this.$emit('close')
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.addAdmin()
                }
            })
        },
        addAdmin() {
            let {username, password, email, phone} = this.ruleForm
            let req = {username, password, email, phone}
            console.log(req)
            // this.$https.get('/data/test.json', req)
            this.$https.post('/user', req)
                .then(function (response) {
                    console.log(req)
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        //
                        this.onClose()
                        this.$emit('addSuccess', '')
                    } else {
                        this.$message.error(res.msg)
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('')
                }.bind(this))
        }
    }
}

</script>

<style></style>