<template>
    <div>
        <h4 class="text-center"></h4>
        <el-row type="flex" justify="space-around" style="margin-bottom:10px">
            <el-col :span="18">
                <!-- <el-input v-model="keyword" clearable placeholder="" style="width:180px"
                    @keydown.enter.native="searchUsers"></el-input> -->
                <el-button type="primary" plain @click="searchUsers"></el-button>
                <el-button type="info" plain @click="requestUsersData"></el-button>
            </el-col>
            <el-col :span="2">
                <!-- <el-button type="success" plain @click="handle"></el-button> -->
            </el-col>
        </el-row>
        <users-table v-bind:userslist="list" style="margin-bottom:10px"
            @cancelEmdit="requestUsersData({}, '')"></users-table>
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
    </div>
</template>

<script>
import UsersTable from '../user/UsersTable.vue';

export default {
    components: { UsersTable },
    name: 'MyUsers',
    data() {
        return {
            currentPage: 1,//
            total: 0,//
            pageSize: 5,//
            list: []//
        }
    },
    methods: {
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.requestUsersData({ currentPage: val })
        },
        searchUsers() {
            let keyword = this.keyword
            this.requestUsersData({ keyword })
        },
        requestUsersData({ currentPage = 1, pageSize = this.pageSize, role = 0 }, successMsg = '') {
            console.log(currentPage, pageSize)
            this.$https.get('/user/page', {
            // this.$https.get('/data/test1.json', {
                params: {
                    currentPage,
                    pageSize,
                    role
                }
            })
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: successMsg,
                            type: 'success'
                        })
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                        console.log(this.list, this.total);
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
                .catch((error) => {
                    console.log(error);
                    this.$message.error('')
                })
        },
        onAddSuccess(msg) {
            this.requestUsersData({}, msg)
        }

    },
    mounted: function () {
        //
        this.requestUsersData({})
    }
}
</script>

<style lang="less" scoped></style>