<template>
    <div class="users_container">
        <el-table :data="userslist" height="520" style="width: 100%" stripe>
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column prop="userId" align="center" label="ID" width="90">
            </el-table-column>
            <el-table-column prop="image" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="username" align="center" label="" width="100">
            </el-table-column>
            <el-table-column prop="name" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="phone" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="email" align="center" label="" width="180">
            </el-table-column>
            <el-table-column prop="gender" align="center" label="" width="50">
            </el-table-column>
            <el-table-column prop="birthday" align="center" label="" width="140">
            </el-table-column>
            <el-table-column align="center" label="" width="140">
                <template slot-scope="scope">
                    <!-- <el-button :type="scope.row.isEdmit ? 'danger' : 'success'" round plain size="small"
                        @click="onEmditClick(scope.row)">
                        {{ scope.row.isEdmit ? '' : '' }}
                    </el-button>
                    <el-button @click="onCancelEmdit" type="text" size="small" v-if="scope.row.isEdmit"></el-button> -->
                    <el-button @click="onDeleteClick(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>

export default {
    name: 'UsersTable',
    props: {
        userslist: {
            type: Array
        }
    },
    data() {
        return {
            multipleSelection: [],
            beforeEdmintData: this.userslist,
            edmintData: {}
        }
    },
    methods: {
        onDeleteClick(row) {
            console.log(row.productId)
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.deleteUsers(row.productId)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        deleteUsers(productId) {
            this.$https.delete('/product/' + productId)
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: '',
                            type: 'success'
                        })
                        //
                        this.onCancelEmdit()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
                .catch((error) => {
                    console.log(error);
                    this.$message.error('')
                })
        }
    }
}
</script>

<style lang="less" scoped></style>