<template>
    <div class="users_container">
        <el-table :data="adminlist" height="520" style="width: 100%" stripe>
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column prop="userId" align="center" label="ID" width="90">
            </el-table-column>
            <el-table-column prop="image" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="username" align="center" label="" width="100">
            </el-table-column>
            <el-table-column prop="name" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="phone" align="center" label="" width="140">
            </el-table-column>
            <el-table-column prop="email" align="center" label="" width="180">
            </el-table-column>
            <el-table-column prop="gender" align="center" label="" width="50">
            </el-table-column>
            <el-table-column prop="birthday" align="center" label="" width="140">
            </el-table-column>
            <el-table-column align="center" label="" width="140">
                <template slot-scope="scope">
                    <el-button type="success" round plain size="small" @click="onEditClick(scope.$index)"></el-button>
                    <el-button @click="onDeleteClick(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>

export default {
    name: 'AdminTable',
    props: {
        adminlist: {
            type: Array
        }
    },
    data() {
        return {
            multipleSelection: [],
            edmintData: {}
        }
    },
    methods: {
        onDeleteClick(row) {
            console.log(row.productId)
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.deleteAdmin(row.userId)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        deleteAdmin(userId) {
            this.$https.delete('/user/' + userId)
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: '',
                            type: 'success'
                        })
                        //
                        this.onCancelEmdit()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
                .catch((error) => {
                    console.log(error);
                    this.$message.error('')
                })
        },
        onEditClick(index) {
            console.log(index)
            //
            this.$emit('onEdit',index)
           
        }

    }
}
</script>

<style lang="less" scoped></style>